.wordcard {
    display: flex;
    justify-content: center;
}
.wordcard__box {
    width: 30vw;
    height: 50vh;
    border-radius: var(--general-border-radius);
    box-shadow: var(--wordcard-box-shadow);
    background-color: var(--wordcard-background-color);
}
.wordcard__close, .wordcard__language, .wordcard__word, .wordcard__transcription, .wordcard__translation {
    display: flex;
    justify-content: center;
}
.wordcard__close {
    margin: 1% 2% 0 0;
    justify-content: flex-end;
}
.wordcard__close:hover {
    cursor: pointer;
    color: var(--header-footer-color);
}
.wordcard__language {
    width: 5vw;
    height: 3vh;
    margin: 0 auto;
    align-items: center;
    color: var(--header-footer-color);
    background-color: var(--word-card-button);
}
.wordcard__word {
    margin-top: 20%;
    margin-bottom: 0;
    font-size: 4rem;
}
.wordcard__translation {
    margin-top: 22%;
    margin-right: 2%;
    justify-content: flex-end;
}
.wordcard__translation button {
    height: 3vh;
    border: var(--header-footer-color);
    border-radius: var(--general-border-radius);
    cursor: pointer;
}
.wordcard__translation button:hover {
    color: var(--header-footer-color);
    background-color: var(--word-card-button);
}
.hidden {
    display: none;
}
.translation {
    margin-top: 0;
    padding: 1%;
    cursor: pointer;
    color: var(--header-footer-color);
    background-color: var(--word-card-button);
}